import { Component, OnInit } from '@angular/core';
import { DataManagerService } from './../data-manager.service';
import { Router } from "@angular/router";
import { NgForm } from '@angular/forms';
import {termEnglish} from './../schemas/englishTerms';
import { ActivatedRoute } from "@angular/router";
import { ReqresTermCreateResponse } from './../schemas/ReqresTermCreateResponse';
import { definition} from './../schemas/ReqresDefiniton';

@Component({
  selector: 'app-definition-create',
  templateUrl: './definition-create.component.html',
  styleUrls: ['./definition-create.component.css']
})
export class DefinitionCreateComponent implements OnInit {

  newTermResult: ReqresTermCreateResponse;
  newDefinition: definition;
  term: termEnglish;

  constructor(private m: DataManagerService, private router: Router, private route: ActivatedRoute) {
    this.newDefinition = new definition();
  }

  ngOnInit() {
    let id = this.route.snapshot.params['id'];
    this.m.getEnglishTerm(id).subscribe(u => this.term= u);
  }

  definitionSave(): void {
    let id = this.route.snapshot.params['id'];
    let dateNow = new Date().toISOString();
    this.newDefinition.dateCreated = dateNow;
    this.m.reqresDefinitonAdd(this.newDefinition, id).subscribe((u) => {  this.router.navigate( ["/termsEnglish/detail/", u._id])});
  }

  onClickDefLike(defId: String){
    this.m.incrementLikesOnDefinition(defId).subscribe(u => this.term= u);
  }
}
