import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from "@angular/common/http";
import { Observable, of } from "rxjs";
import { catchError, tap } from "rxjs/operators";
import {termEnglish} from './schemas/englishTerms';
import {otherTerms} from './schemas/otherTerms'
import { ReqresHelpResponse} from './schemas/ReqresHelpResponse';
import { ReqresTermCreate} from './schemas/ReqresTermCreate';
import { ReqresTermCreateResponse } from './schemas/ReqresTermCreateResponse';
import { ReqresTermOtherCreate} from './schemas/ReqresTermOtherCreate';
import { ReqresTermOtherCreateResponse } from './schemas/ReqresTermOtherCreateResponse';
import { definition} from './schemas/ReqresDefiniton';
@Injectable({
  providedIn: 'root'
})
export class DataManagerService {

  constructor(private http: HttpClient) { }

  private url: string = 'https://dict-api.herokuapp.com';

  private httpOptions = {
    headers: new HttpHeaders({
      'Content-Type': 'application/json'
    })
  };

  private handleError<T> (operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {
      console.error(error);
      return of(result as T);
    };
  }

  getEnglishTerms() : Observable<termEnglish[]>{
      return this.http.get<termEnglish[]>(`${this.url}/terms/english`);
  }

  getEnglishTermsByWord(word: String) : Observable<termEnglish[]>{
    return this.http.get<termEnglish[]>(`${this.url}/terms/english/${word}`);
  }

  getEnglishTerm(id : String) : Observable<termEnglish>{
    return this.http.get<termEnglish>(`${this.url}/term/english/${id}`);
  }
  getOtherTerm(id : String) : Observable<otherTerms>{
    return this.http.get<otherTerms>(`${this.url}/term/other/${id}`);
  }

  getEnglishTermByWord(word : String) : Observable<termEnglish>{
    return this.http.get<termEnglish>(`${this.url}/term/english/${word}`);
  }

  getOtherTermsByEnglishId(id : String) : Observable<otherTerms[]>{
    return this.http.get<otherTerms[]>(`${this.url}/terms/other/${id}`);
  }

  incrementHelpNo(id: number): Observable< ReqresHelpResponse> {
    return this.http.put< ReqresHelpResponse>(`${this.url}/terms/english/helpNo/${id}`, this.httpOptions)
  }

  incrementHelpYes(id: number): Observable< ReqresHelpResponse> {
    return this.http.put< ReqresHelpResponse>(`${this.url}/terms/english/helpYes/${id}`, this.httpOptions)
  }

  incrementLikesOnDefinition(id: String): Observable< ReqresHelpResponse> {
    return this.http.put< ReqresHelpResponse>(`${this.url}/terms/english/definition-like/${id}`, this.httpOptions)
  }
  incrementOtherHelpNo(id: number): Observable< ReqresHelpResponse> {
    return this.http.put< ReqresHelpResponse>(`${this.url}/terms/other/helpNo/${id}`, this.httpOptions)
  }

  incrementOtherHelpYes(id: number): Observable< ReqresHelpResponse> {
    return this.http.put< ReqresHelpResponse>(`${this.url}/terms/other/helpYes/${id}`, this.httpOptions)
  }

  incrementLikesOnOtherDefinition(id: String): Observable< ReqresHelpResponse> {
    return this.http.put< ReqresHelpResponse>(`${this.url}/terms/other/definition-like/${id}`, this.httpOptions)
  }

  reqresTermAdd(newTerm: ReqresTermCreate): Observable<ReqresTermCreateResponse> {
    return this.http.post<ReqresTermCreateResponse>(`${this.url}/terms/english`, newTerm, this.httpOptions)
      .pipe(
        tap((newTerm: ReqresTermCreateResponse) => console.log(`${newTerm}`)),
        catchError(this.handleError<ReqresTermCreateResponse>('Term add'))
      );
  }

  reqresTermOtherAdd(newTerm: ReqresTermOtherCreate): Observable<ReqresTermOtherCreateResponse> {
    return this.http.post<ReqresTermOtherCreateResponse>(`${this.url}/terms/other`, newTerm, this.httpOptions)
      .pipe(
        tap((newTerm: ReqresTermOtherCreateResponse) => console.log(`${newTerm}`)),
        catchError(this.handleError<ReqresTermOtherCreateResponse>('Other Term add'))
      );
  }

  reqresDefinitonAdd(newDefiniton: definition, id: String): Observable<ReqresTermCreateResponse> {
    return this.http.put<ReqresTermCreateResponse>(`${this.url}/terms/english/${id}/add-definition`, newDefiniton, this.httpOptions)
      .pipe(
        tap((newTerm: ReqresTermCreateResponse) => console.log(`${newTerm}`)),
        catchError(this.handleError<ReqresTermCreateResponse>('Definition add'))
      );
  }

  reqresDefinitonOtherAdd(newDefiniton: definition, id: String): Observable<ReqresTermOtherCreateResponse> {
    return this.http.put<ReqresTermOtherCreateResponse>(`${this.url}/terms/other/${id}/add-definition`, newDefiniton, this.httpOptions)
      .pipe(
        tap((newTerm: ReqresTermOtherCreateResponse) => console.log(`${newTerm}`)),
        catchError(this.handleError<ReqresTermOtherCreateResponse>('Definition add'))
      );
  }
  
}
