import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from "@angular/router";
import languageCodes from './../languageCodes/langCodes.json';
import { DataManagerService } from "../data-manager.service";
import { termEnglish} from "../schemas/englishTerms";
import { otherTerms} from "../schemas/otherTerms";

@Component({
  selector: 'app-term-detail',
  templateUrl: './term-detail.component.html',
  styleUrls: ['./term-detail.component.css']
})
export class TermDetailComponent implements OnInit {

  
  term: termEnglish;
  termsOther: otherTerms[];
  languageCodes= languageCodes;

  constructor(private m: DataManagerService, private route: ActivatedRoute) { 
   
  }

  ngOnInit() {
    let id = this.route.snapshot.params['id'];
    this.m.getEnglishTerm(id).subscribe(u => this.term= u);
    this.m.getOtherTermsByEnglishId(id).subscribe((u) => {
      this.termsOther= u;
      for (let lang  of languageCodes) {
        for (let term  of this.termsOther) {
          if(term.languageCode === lang.languageCode){
              term.languageCode = lang.language;
          }
        }
      }

    });
  }

  onClickIncrementHelpNo(){
    let id = this.route.snapshot.params['id'];
    this.m.incrementHelpNo(id).subscribe(u => this.term= u);
  }

  onClickIncrementHelpYes(){
    let id = this.route.snapshot.params['id'];
    this.m.incrementHelpYes(id).subscribe(u => this.term= u);
  }

  onClickDefLike(defId: String){
    this.m.incrementLikesOnDefinition(defId).subscribe(u => this.term= u);
  }

}
