import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { HomeComponent } from "./home/home.component";
import { AboutComponent } from './about/about.component';
import { ContactComponent } from "./contact/contact.component";
import { NotFoundComponent } from "./not-found/not-found.component";
import { TermsEnglishComponent } from "./termsEnglish/termsEnglish.component";
import { TermDetailComponent } from './term-detail/term-detail.component';
import { TermOtherDetailComponent } from './term-other-detail/term-other-detail.component';
import { TermCreateComponent } from './term-create/term-create.component';
import { TermOtherCreateComponent } from './term-other-create/term-other-create.component';
import { DefinitionCreateComponent } from './definition-create/definition-create.component';
import { OtherDefinitionCreateComponent } from './other-definition-create/other-definition-create.component';

const routes: Routes = [
  { path: 'home', component: HomeComponent },
  { path: 'about', component: AboutComponent },
  { path: 'contact', component: ContactComponent },
  { path: 'termsEnglish/create', component: TermCreateComponent },
  { path: 'termsEnglish', component: TermsEnglishComponent },
  { path: 'termsEnglish/detail/:id', component: TermDetailComponent },
  { path: 'termsEnglish/add-definition/:id', component: DefinitionCreateComponent },
  { path: 'termsOther/create/:id', component: TermOtherCreateComponent },
  { path: 'termsOther/detail/:id', component: TermOtherDetailComponent},
  { path: 'termsOther/add-definition/:id', component: OtherDefinitionCreateComponent},
  // more routes are added here
  { path: '', redirectTo: '/home', pathMatch: 'full' },
  { path: '**', component: NotFoundComponent }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
