import { Component, OnInit } from '@angular/core';
import { DataManagerService } from "./../data-manager.service";
//import { Observable } from "rxjs";
import { termEnglish} from "./../schemas/englishTerms";

@Component({
  selector: 'app-termsEnglish',
  templateUrl: './termsEnglish.component.html',
  styleUrls: ['./termsEnglish.component.css']
})
export class TermsEnglishComponent implements OnInit {

  constructor(private m: DataManagerService) { }
  termsEnglish: termEnglish[];
  searchText:String;
  
  ngOnInit() {
    this.searchText='';
    this.m.getEnglishTerms().subscribe(t => this.termsEnglish = t)
  }

  doSearch(){
    console.log(this.searchText.length)
    if(this.searchText!==undefined && this.searchText.length >= 2){
      this.m.getEnglishTermsByWord(this.searchText).subscribe(t => this.termsEnglish = t)  
    }

    if(this.searchText.length === 1){
      this.m.getEnglishTerms().subscribe(t => this.termsEnglish = t) 
    }
  }
}
