import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from "@angular/router";
import languageCodes from './../languageCodes/langCodes.json';
import { DataManagerService } from "../data-manager.service";
import { termEnglish} from "../schemas/englishTerms";
import { otherTerms} from "../schemas/otherTerms";

@Component({
  selector: 'app-term-other-detail',
  templateUrl: './term-other-detail.component.html',
  styleUrls: ['./term-other-detail.component.css']
})
export class TermOtherDetailComponent implements OnInit {

  
  term: otherTerms;
  

  constructor(private m: DataManagerService, private route: ActivatedRoute) { 
   
  }

  ngOnInit() {
    let id = this.route.snapshot.params['id'];
    this.m.getOtherTerm(id).subscribe(u => this.term= u);
    console.log(this.term);
  }

  onClickIncrementHelpNo(){
    let id = this.route.snapshot.params['id'];
    this.m.incrementOtherHelpNo(id).subscribe(u => this.term= u);
    
  }

  onClickIncrementHelpYes(){
    let id = this.route.snapshot.params['id'];
    console.log("Piku pagal hai");
    this.m.incrementOtherHelpYes(id).subscribe(u => this.term= u);
  }

  onClickDefLike(defId: String){
    this.m.incrementLikesOnOtherDefinition(defId).subscribe(u => this.term= u);
  }


  

}
