import { Component, OnInit } from '@angular/core';
import { DataManagerService } from './../data-manager.service';
import { Router } from "@angular/router";
import { ActivatedRoute } from "@angular/router";
import { ReqresTermOtherCreateResponse } from './../schemas/ReqresTermOtherCreateResponse';
import { definition} from './../schemas/ReqresDefiniton';
import { NgForm } from '@angular/forms';
import { otherTerms } from '../schemas/otherTerms';

@Component({
  selector: 'app-other-definition-create',
  templateUrl: './other-definition-create.component.html',
  styleUrls: ['./other-definition-create.component.css']
})
export class OtherDefinitionCreateComponent implements OnInit {

  newTermResult: ReqresTermOtherCreateResponse;
  newDefinition: definition;
  term: otherTerms;


  constructor(private m: DataManagerService, private router: Router, private route: ActivatedRoute) {
    this.newDefinition = new definition();
  }

  ngOnInit() {
    let id = this.route.snapshot.params['id'];
    this.m.getOtherTerm(id).subscribe(u => this.term= u);
  }

  definitionOtherSave(): void {
    let id = this.route.snapshot.params['id'];
    let dateNow = new Date().toISOString();
    this.newDefinition.dateCreated = dateNow;
    this.m.reqresDefinitonOtherAdd(this.newDefinition, id).subscribe((u) => {  this.router.navigate( ["/termsOther/detail/", u._id])});
  }

  onClickDefLike(defId: String){
    this.m.incrementLikesOnOtherDefinition(defId).subscribe(u => this.term= u);
  }

}
