import { Component, OnInit } from '@angular/core';
import { DataManagerService } from './../data-manager.service';
import { Router } from "@angular/router";
import { NgForm } from '@angular/forms';
import languageCodes from './../languageCodes/langCodes.json';
import {termEnglish} from './../schemas/englishTerms';
import { ActivatedRoute } from "@angular/router";
import { ReqresTermOtherCreate } from "./../schemas/ReqresTermOtherCreate"
import { ReqresTermOtherCreateResponse } from "./../schemas/ReqresTermOtherCreateResponse"
import { definition } from "./../schemas/ReqresDefiniton"

@Component({
  selector: 'app-term-other-create',
  templateUrl: './term-other-create.component.html',
  styleUrls: ['./term-other-create.component.css']
})
export class TermOtherCreateComponent implements OnInit {

  newTerm: ReqresTermOtherCreate;
  newTermResult: ReqresTermOtherCreateResponse;
  newDefinition: definition;
  term:termEnglish
  languageCodes= languageCodes;

  constructor(private m: DataManagerService, private router: Router, private route: ActivatedRoute) {
    this.newTerm = new ReqresTermOtherCreate();
    this.newTermResult= new ReqresTermOtherCreateResponse();
    this.newDefinition = new definition();
  }
  

  ngOnInit() {
    let id = this.route.snapshot.params['id'];
    this.m.getEnglishTerm(id).subscribe(u => this.term= u);
    
  }

  termOtherSave(): void {
    let dateNow = new Date().toISOString();
    this.newTerm.dateCreated = dateNow;
    this.newTerm.dateRevised = dateNow;
    this.newDefinition.dateCreated = dateNow;
    this.newDefinition.authorName = this.newTerm.authorName;
    this.newTerm.definitions = new Array();
    this.newTerm.definitions.push(this.newDefinition);
    this.newTerm.termEnglishId = this.route.snapshot.params['id'];
    this.newTerm.wordEnglish = this.term.wordEnglish;
    this.m.reqresTermOtherAdd(this.newTerm).subscribe((u) => {  this.router.navigate( ["/termsOther/detail/", u._id])});
  }
}
