import { Component, OnInit } from '@angular/core';
import { DataManagerService } from './../data-manager.service';
import { Router } from "@angular/router";
import { NgForm } from '@angular/forms';
import { ReqresTermCreate } from './../schemas/ReqresTermCreate';
import { ReqresTermCreateResponse } from './../schemas/ReqresTermCreateResponse';
import { definition} from './../schemas/ReqresDefiniton';

@Component({
  selector: 'app-term-create',
  templateUrl: './term-create.component.html',
  styleUrls: ['./term-create.component.css']
})
export class TermCreateComponent implements OnInit {

  newTerm: ReqresTermCreate;
  newTermResult: ReqresTermCreateResponse;
  newDefinition: definition;

  constructor(private m: DataManagerService, private router: Router) {
    this.newTerm = new ReqresTermCreate();
    this.newTermResult= new ReqresTermCreateResponse();
    this.newDefinition = new definition();
  }

  ngOnInit() { 
  }
 
  termSave(): void {
    let dateNow = new Date().toISOString();
    this.newTerm.languageCode ='en';
    this.newTerm.dateCreated = dateNow;
    this.newTerm.dateRevised = dateNow;
    this.newDefinition.dateCreated = dateNow;
    this.newDefinition.authorName = this.newTerm.authorName;
    this.newTerm.definitions = new Array();
    this.newTerm.definitions.push(this.newDefinition);
    this.m.reqresTermAdd(this.newTerm).subscribe((u) => {  this.router.navigate( ["/termsEnglish/detail/", u._id])});
  }

}
